.footer {
	height: 100px;
	background-color: #2E2E2E;
  padding: 20px 0;
  display: -ms-flexbox;
  display: flex;
}

.footer__container {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
	-ms-flex-align: center;
	    align-items: center;
}

.footer__text {
	font-size: 1.3em;
	color: #9E9E9E;
	font-family: 'Open Sans';
	font-style: italic;
}

.footer__hotmart-logo {
	width: 150px;
}

.footer__icons {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
	-ms-flex-align: center;
	    align-items: center;
	padding: 4px 0;
}

.margin_0 {
	margin:	0;
}

.footer__logo-icons__container {
	display: -ms-flexbox;
	display: flex;
	width: 60%;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
	-ms-flex-align: center;
	    align-items: center;
}

@media screen and (max-width: 600px) {
  .footer {
		height: auto;
	}

	.footer__container {
		display: -ms-flexbox;
		display: flex;
		-ms-flex-direction: column;
		    flex-direction: column;
		-ms-flex-align: center;
		    align-items: center;
	}

	.footer__hotmart-logo {
		padding: 20px 0 20px;
  }
  
  .footer__hotmart-logo {
		width: 100px;
	}

	.footer__logo-icons__container {
		width: 90%;
	}

	.footer__hotmart-logo__container {
		display: inline-block;
	}

	.footer__icons {
		display: inline-block;
    width: 100px;
    margin-top: 20px;
	}
}

@media screen and (min-width: 601px) and (max-width: 767px) {
  .footer__hotmart-logo {
		width: 100px;
	}

	.footer__text {
		width: 150px;
	}
}

@media only screen and (min-width: 768px) and (max-width: 992px) {
	.footer__hotmart-logo {
		width: 100px;
	}

	.footer__text {
		width: 150px;
  }
}


